.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: relative;
  top: 40vh;
  left: 47%;
  scale: 5;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border: 2px solid #000;
  border-radius: 50%;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: 1s;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

